  .admin-popup {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 150px;

    h2 {
      @include font-settings ($ff-inter, 500, 40px, 1.2);
      color: #D1D2D3;
      margin-bottom: 7px;
      text-align: center;
    }

    p {
      @include font-settings ($ff-inter, 500, 16px, 1.5);
      text-align: center;
      margin-bottom: 26px;
    }

    form {
      display: flex;
      flex-direction: column;
      position: relative;

      label {
        position: relative;
      }

      input {
        @include font-settings ($ff-inter, 400, 16px, 1.5);
        width: 288px;
        height: 56px;
        background: #1A1D25;
        border: 1px solid #76777C;
        border-radius: 12px;
        padding: 16px;
        margin-bottom: 19px;

        &::placeholder {
          color: #A3A5A8;
        }

        &:focus-visible,
        &:hover {
          border: 1px solid $color-accent;
          outline: none;
        }
      }
    }
  }

  .admin-popup__signup-btn {
    background-color: transparent;
    border: none;

    span {
      @include font-settings ($ff-inter, 500, 16px, 1.5);
      color: $color-default-white;
      text-decoration: underline;
      cursor: pointer;
    }
  }

  .admin-popup__password-toggle-btn {
    @include font-settings ($ff-inter, 400, 14px, 1.43);
    position: absolute;
    top: 92px;
    right: 11px;
    background-color: transparent;
    border: none;
    text-decoration: underline;
    color: #76777C;
    cursor: pointer;
  }

  .admin-popup__submit-btn {
    @include font-settings ($ff-inter, 500, 14px, 1);
    opacity: 0.8;
    width: 100%;
    height: 52px;
    background-color: $color-accent;
    color: $color-default-black;
    border: none;
    border-radius: 5px;
    cursor: pointer;

    &:hover {
      background-color: $color-accent-hover;
    }

    &:active {
      opacity: 0.5;
    }
  }
