.popup-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  opacity: 0;
  pointer-events: none;
  transition: 0.5s all;
  z-index: 1000;
}

.popup-overlay.active {
  display: block;
  opacity: 1;
  pointer-events: all;
  transition: 0.5s all;
}

.popup {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: 0.5s all;
  min-width: 925px;
  min-height: 698px;
  background-color: #262931;
  border-radius: 20px;
  padding: 31px;
  z-index: 1000;
}

.popup.active {
  transform: translate(-50%, -50%) scale(1);
  transition: 0.5s all;
}

.popup__close-btn {
  position: absolute;
  top: -30px;
  right: -30px;
  width: 45px;
  height: 45px;
  border: none;
  border-radius: 50%;
  background-color: transparent;
  cursor: pointer;
}
.popup__close-btn::after, .popup__close-btn::before {
  position: absolute;
  content: "";
  top: 18px;
  left: 8px;
  width: 30px;
  height: 2px;
  background-color: #ffffff;
  opacity: 0.5;
  transform: rotate(45deg);
}
.popup__close-btn::after {
  transform: rotate(135deg);
}

.popup__content-item {
  display: none;
