.set-rating-overlay {
  margin-top: 22px;
  background: #262931;
  border-radius: 10px;
  padding: 40px;
}

.set-rating fieldset {
  margin: 0 0 1em 0;
  padding: 0;
  border: none;
}

/* Group */
.set-rating__group {
  position: relative;
  width: 92px;
  height: 14px;
  background-image: url("../img/svg/set-rating-grey.svg");
  background-size: 18px 14px;
  background-repeat: repeat-x;
}

/* Input */
.set-rating__input {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.set-rating input:focus-within + label,
.set-rating input:focus-visible + label {
  outline: 1px solid #fccd03;
  outline-offset: 0.2em;
}

.set-rating__star:focus ~ .set-rating__focus,
label:focus ~ .set-rating__focus {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  border: 0.2em solid #fccd03;
  outline-offset: 0.2em;
}

/* Star */
.set-rating__star {
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  height: 14px;
  background-size: 18px 14px;
  background-repeat: repeat-x;
  cursor: pointer;
}
.set-rating__star:hover {
  opacity: 0.5;
}

.set-rating__star:hover,
.set-rating__input:checked + .set-rating__star {
  background-image: url("../img/svg/set-rating-gold.svg");
  outline: none;
}

.set-rating__star:hover ~ .set-rating__star {
  background-image: url("../img/svg/set-rating-grey.svg");
}

/* Options */
.set-rating__star:nth-of-type(1) {
  z-index: 5;
  width: 18px;
}

.set-rating__star:nth-of-type(2) {
  z-index: 4;
  width: 36px;
}

.set-rating__star:nth-of-type(3) {
  z-index: 3;
  width: 54px;
}

.set-rating__star:nth-of-type(4) {
  z-index: 2;
  width: 72px;
}

.set-rating__star:nth-of-type(5) {
  z-index: 1;
  width: 90px;
}

.filters__group input:hover + span,
.filters__group input:active + span {
  outline: none;
}
.filters__group input:focus-visible + span {
  outline: 1px solid #fccd03;
  outline-offset: 0.2em;
}

.select fieldset {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  border: none;
}

.select__title {
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.1;
  color: #ffffff;
  display: block;
  opacity: 0.5;
  margin-bottom: 10px;
}

.choices__inner {
  height: 48px;
  background: #1A1D25;
  border: 1px solid #76777C;
  border-radius: 8px;
  margin-bottom: 7px;
}

.select-one .choices__inner {
  padding: 8px 30px 8px 16px;
}

.select-multiple .choices__inner {
  padding: 12px 100px 12px 16px;
}

.choices:hover .choices__inner,
.choices:active .choices__inner,
.choices.is-focused .choices__inner,
.choices.is-open .choices__inner {
  border: 1px solid #FCCD03;
  border-radius: 8px;
}

.choices__list--dropdown,
.choices__list[aria-expanded] {
  background: #1A1D25;
  box-shadow: 0px 8px 24px -6px rgba(0, 0, 0, 0.16), 0px 0px 1px rgba(0, 0, 0, 0.4);
  border: none;
  border-radius: 12px;
  z-index: 100;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted,
.choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
  background-color: #33363d;
}

.choices[data-type*=select-one]::after,
.choices[data-type*=select-multiple]::after {
  position: absolute;
  content: url("../img/svg/select-control.svg");
  border: none;
  margin: 0;
  cursor: pointer;
}

.choices[data-type*=select-one]::after {
  top: 16px;
  right: 30px;
}

.choices[data-type*=select-one].is-open::after {
  transform: scale(1, -1);
  top: 40px;
}

.choices[data-type*=select-multiple]::after {
  top: 16px;
  right: 15px;
}

.choices[data-type*=select-multiple].is-open::after {
  transform: scale(1, -1);
  top: 10px;
}

.choices__list--single .choices__item {
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.71;
  color: #ffffff;
  color: #D1D2D3;
}

#choices--choices-single-default-item-choice-1,
#choices--choices-single-default-2-item-choice-1 {
  display: none;
}

.choices[data-type*=select-multiple] .choices__inner {
  overflow: hidden;
  cursor: pointer;
}

.choices__list--multiple .choices__item {
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 10px;
  line-height: 1.6;
  color: #ffffff;
  height: 24px;
  padding: 4px 4px 4px 8px;
  background-color: #FCCD03;
  color: #000000;
  border: none;
  border-radius: 4px;
  margin-bottom: 0;
  position: relative;
}

.choices__list--multiple .choices__item {
  margin-bottom: 12px;
}
.choices__list--multiple .choices__item.is-highlighted {
  background-color: #FCCD03;
  border: none;
}

.choices[data-type*=select-multiple] .choices__button {
  width: 16px;
  height: 16px;
  padding: 0;
  margin: 0;
  margin-left: 4px;
  border: none;
  background-image: url("../img/svg/select-close-icon.svg");
  background-size: 16px;
}

.choices__inner input[type=search] {
  display: none;
}

.select-multiple__controls {
  position: absolute;
  display: flex;
  align-items: center;
  top: 38px;
  right: 43px;
}

.select-multiple__reset-btn {
  width: 16px;
  height: 16px;
  background-color: transparent;
  border: none;
  background-image: url("../img/svg/select-reset-btn.svg");
  cursor: pointer;
}

.popover {
  opacity: 0;
}

.popover.active {
  position: relative;
  display: block;
  min-width: 33px;
  background-color: #262832;
  border-radius: 8px;
  margin-right: 7px;
  padding: 4px 8px;
  cursor: pointer;
  opacity: 1;
}
.popover.active p {
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 12px;
  line-height: 1.33;
  color: #ffffff;
}

.popover__content {
  display: none;
}

.popover:hover .popover__content {
  position: absolute;
  display: flex;
  flex-wrap: nowrap;
  top: -55px;
  right: -81px;
  min-width: 200px;
  background-color: #1A1D25;
  border: 1px solid #1A1D25;
  border-radius: 4px;
  padding: 12px;
}
.popover:hover .popover__content span {
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.33;
  color: #ffffff;
  color: #D1D2D3;
  display: inline;
  white-space: nowrap;
}

.static-content {
  margin-top: 20px;
  background: #1A1D24;
  border: 1px solid #2D3341;
  border-radius: 10px;
  padding: 36px 45px 30px;
}

.static-content__heading h2 {
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 60px;
  line-height: 1.2;
  color: #ffffff;
  letter-spacing: 0.0075em;
  color: #D1D2D3;
  margin-bottom: 20px;
}

.static-content__text h3 {
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.56;
  color: #ffffff;
}
.static-content__text p {
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.56;
  color: #ffffff;
  color: #D1D2D3;
  margin-bottom: 27px;
}

.alert-popup {
  display: none;
}

.alert-popup.active {
  display: block;
  position: fixed;
  bottom: 12px;
  right: 36px;
  transition: 0.5s all;
  min-width: 360px;
  border-radius: 6px;
  z-index: 1000;
}

.alert-popup__content {
  border-radius: 6px;
  box-shadow: 0px 12px 24px -6px rgba(96, 96, 96, 0.15), 0px 0px 1px rgba(96, 96, 96, 0.1);
  padding: 24px;
}
.alert-popup__content--error {
  background-color: #991212;
}
.alert-popup__content--success {
  background-color: #0d6f0d;
}
.alert-popup__content p {
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 1;
  color: #ffffff;
}

.alert-popup__close-btn {
  position: absolute;
  top: -10px;
  right: -18px;
  content: "";
  background-color: transparent;
  border: none;
  width: 20px;
  height: 20px;
  cursor: pointer;
}
.alert-popup__close-btn::before, .alert-popup__close-btn::after {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  width: 20px;
  height: 2px;
  background-color: #ffffff;
  opacity: 0.6;
}
.alert-popup__close-btn::before {
  transform: rotate(45deg);
}
.alert-popup__close-btn::after {
  transform: rotate(135deg);
}